import React, { PureComponent, Fragment } from 'react';
import moment from 'moment';
import $ from "jquery";
import './error.css';
import { ERROR_BACKGROUND_IMG, ERROR_ERROR, API_ROOT_URL, V_EVENT } from '../../constants'

const styles = {
    addBtn: {
        border: '1px solid #2b2b2b',
        color: '#2b2b2b',
        fontSize: '14px',
        textTransform: 'uppercase',
        padding: '12px 20px',
        borderRadius: '5px', margin: 8
    },
    showDemoPost: {
        fontSize: '14px',
        textTransform: 'uppercase',
        padding: '12px 20px',
        borderRadius: '5px', margin: 8, color: '#fff',
        border: 'none'
    },
    demoWallErrorStyle: {
        maxWidth: '100%',
        width: '100%',
        //left: 'auto',
        //  right: '0px',
        transform: 'none',
        bottom: '0',
        // top: '0px',
        zIndex: 9998,
        position: 'fixed',
        padding: '15px 15px',
        //  borderTop: '1px solid rgb(244, 244, 244)',
        borderRadius: '0px',
        textAlign: 'center',
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'center',
        background: 'rgb(26, 32, 41)'
    },
    hideBtnStyle: {
        color: 'rgb(194, 14, 94)',
        textTransform: 'capitalize',
        background: 'transparent',
        borderWidth: '1px',
        borderStyle: 'solid',
        borderColor: 'rgb(194, 14, 94)',
        borderImage: 'initial',
        padding: '10px 15px',
        margin: '3px'
    },
    strong: {
        color: '#c20e5e',
        fontWeight: 600
    }
}
export default class Error extends PureComponent {
    onClickToadd = event => {
        event.preventDefault();
        let data = [];
        data['type'] = 'create_feed';
        window.parent.postMessage(data, '*');
    }

    onClickToDemoPost = event => {
        event.preventDefault();
        const { newApi } = this.props;
        let wallId = newApi.wall.Wall.id;
        let status = newApi.wall.Personalization.demoWall;
        $.ajax({
            url: `${API_ROOT_URL}display/wall/demoStatus/${wallId}/${status}`,
            cache: false,
            dataType: "json",
            timeout: (1000 * 60 * 7),
            success: function (response) {
                window.location.reload()
            },
            error: function (XMLHttpRequest, textStatus, errorThrown) {
                console.log("demoStatus error", textStatus, errorThrown)
            }

        });

    }

    onClicktoUrl = (path) => event => {
        event.preventDefault();
        window.top.location.href = path;
    }

    onClickHref = (e) => {
        e.preventDefault()

    }


    render() {
        const { err, newApi, wallErrorCode, networkHandle, olderDays } = this.props;
        let editor = window.editor ? true : window.location.pathname.includes('/editor');
        let urltagg = 'https://test.taggbox.com';
        // console.log("newApi.error_code", newApi.error_code, wallErrorCode)

        return (

            <Fragment>
                {
                    err === 404 ?
                        <Fragment>
                            <div className="col-md-12 text-center noPostSec" id="noPosts"
                                style={{ backgroundImage: 'url(' + ERROR_BACKGROUND_IMG + ')' }}>
                                <div className="noPostContent">
                                    <div className="noPostMsgBlock" >
                                        <h2> <a href={urltagg} target="_blank" rel="noopener noreferrer"> Wall is not active.</a></h2>
                                    </div>
                                </div>
                            </div>
                        </Fragment>

                        : newApi.error_code === 201 || wallErrorCode === 201 ?
                            <Fragment>
                                <div className={editor ? `erro_204_fix` : ``}>
                                    <h2>No Posts!</h2>
                                    <p>If the Moderation is ON, approve the posts from the moderation section to make them appear on the social wall.</p>
                                    <p>Else, there wont be any posts on your added feeds.</p>
                                </div>
                            </Fragment>

                            : newApi.error_code === 202 || wallErrorCode === 202 ?
                                <Fragment>
                                    {newApi.wall.Personalization.demoWall === 1 && editor ? <div style={{ boxShadow: 'rgba(0, 0, 0, 0.3) 0px 3px 10px', ...styles.demoWallErrorStyle }}><h2 style={{ display: 'none' }}>No Feeds!</h2>
                                        <p style={{
                                            fontFamily: 'Open Sans",sans-serif', fontSize: '18px',
                                            color: 'rgb(110, 112, 115)',
                                            fontWeight: 300,
                                            margin: '0px'
                                        }}>Seems like you have not added any feed. Kindly add feed from your preferred social network.</p><span style={{ padding: '0px 15px' }}><a href class="loadDemoWall btn hideBtn" onClick={this.onClickToDemoPost}>HIDE DEMO POSTS</a></span></div> :

                                        <Fragment>
                                            <h2>No Feeds!</h2>
                                            <p style={{ fontFamily: 'Open Sans",sans-serif' }}>Seems like you have not added any feed. Kindly add feed from your preferred social network.</p>
                                            {editor ? <div style={{ margin: '20px 0 0' }}>
                                                <a href className="btn transBtn create-new-feed" style={styles.addBtn} onClick={this.onClickToadd}>
                                                    Add Feed </a>
                                                <a href className="loadDemoWall btn commanBtn" style={styles.showDemoPost} onClick={this.onClickToDemoPost}>SHOW DEMO POSTS</a>
                                            </div> : null}

                                        </Fragment>}
                                </Fragment>

                                : newApi.error_code === 203 || wallErrorCode === 203 ?
                                    <Fragment>
                                        {newApi.wall.Personalization.demoWall === 1 && editor ? <div style={{ boxShadow: 'rgba(0, 0, 0, 0.3) 0px 3px 10px', ...styles.demoWallErrorStyle }}><h2 style={{ display: 'none' }}>Feed Not Activated!</h2>
                                            <p style={{
                                                fontFamily: 'Open Sans",sans-serif', fontSize: '16px',
                                                color: 'rgb(255, 255, 255)',
                                                fontWeight: 400,
                                                margin: '0px'
                                            }}> Looks like your feed status is off. Click here to turn the status on! Or add another feed. <a href className="manage-feed" onClick={this.onClicktoUrl(`${API_ROOT_URL}events/wall/index/${newApi.wall.Wall.id}`)} style={{ cursor: 'pointer', color: '#ff4051' }}> Wall editor</a></p>
                                            <span style={{ padding: '0px 15px' }}><a href className="loadDemoWall btn hideBtn" onClick={this.onClickToDemoPost}>HIDE DEMO POSTS</a></span></div> :

                                            <Fragment>
                                                <h2>Feed Not Activated!</h2>
                                                <p> Looks like your feed status is off. Click here to turn the status on! Or add another feed.
                            {/* <a href onClick={this.onClicktoUrl(`${API_ROOT_URL}events/wall/index/${newApi.wall.Wall.id}`)} className="manage-feed" style={{ cursor: 'pointer' }}> wall editor</a> */}
                                                </p>

                                                {editor ? <div style={{ margin: '20px 0 0' }}>
                                                    <a href className="btn transBtn create-new-feed" style={styles.addBtn} onClick={this.onClickToadd}>
                                                        Add Another Feed </a>
                                                    <a href className="loadDemoWall btn commanBtn" style={styles.showDemoPost} onClick={this.onClickToDemoPost}>SHOW DEMO POSTS</a>
                                                </div> : null}
                                            </Fragment>}
                                    </Fragment>

                                    : newApi.error_code === 204 || wallErrorCode === 204 ?
                                        <Fragment>

                                            <div className={editor ? `erro_204_fix` : ``}>
                                                <h2>Feed Not Authorized!</h2>
                                                <p>It appears that you have not authorized any Feed. Please <a href={editor ? `#` : `${API_ROOT_URL}events/wall/index/${newApi.wall.Wall.id}`} onClick={editor ? this.onClicktoUrl(`${API_ROOT_URL}events/wall/index/${newApi.wall.Wall.id}`) : null} className="manage-feed">Click here</a> to authorize now!</p>
                                            </div>
                                        </Fragment>

                                        : newApi.error_code === 205 || wallErrorCode === 205 ?
                                            <Fragment>
                                                {newApi.wall.Personalization.demoWall === 1 && editor ? <div style={{ boxShadow: 'rgba(0, 0, 0, 0.3) 0px 3px 10px', ...styles.demoWallErrorStyle }}><h2 style={{ display: 'none' }}> No Tweets/Posts!</h2>
                                                    <p style={{
                                                        fontFamily: 'Open Sans",sans-serif', fontSize: '18px',
                                                        color: 'rgb(110, 112, 115)',
                                                        fontWeight: 300,
                                                        margin: '0px'
                                                    }}>No Tweets/Posts are available on the entered <storng style={styles.strong}>{ERROR_ERROR[`${olderDays}`]}{networkHandle}</storng>. The upcoming Tweets/posts will start appearing here.</p><span style={{ padding: '0px 15px' }}><a href class="loadDemoWall btn hideBtn" onClick={this.onClickToDemoPost}>HIDE DEMO POSTS</a></span></div> :
                                                    <Fragment>
                                                        <h2> No Tweets/Posts!</h2>
                                                        <p>No Tweets/Posts are available on the entered <storng style={styles.strong}>{ERROR_ERROR[`${olderDays}`]}{networkHandle}</storng>. The upcoming Tweets/posts will start appearing here.</p>
                                                        <p class="tweets-or-section">OR</p>
                                                        <p>you can click here to add another feed.</p>
                                                        {editor ? <div style={{ margin: '20px 0 0' }}>
                                                            <a href className="btn transBtn create-new-feed" style={styles.addBtn} onClick={this.onClickToadd}>
                                                                Add Another Feed </a>
                                                            <a href className="loadDemoWall btn commanBtn" style={styles.showDemoPost} onClick={this.onClickToDemoPost}>SHOW DEMO POSTS</a>
                                                        </div> : null}
                                                    </Fragment>}
                                            </Fragment>

                                            : newApi.error_code === 206 || wallErrorCode === 206 ?
                                                <Fragment>
                                                    {newApi.wall.Personalization.demoWall === 1 && editor ? <div style={{ boxShadow: 'rgba(0, 0, 0, 0.3) 0px 3px 10px', ...styles.demoWallErrorStyle }}><h2 style={{ display: 'none' }}>No Tweets!</h2>
                                                        <p style={{
                                                            fontFamily: 'Open Sans",sans-serif', fontSize: '18px',
                                                            color: 'rgb(110, 112, 115)',
                                                            fontWeight: 300,
                                                            margin: '0px'
                                                        }}>No Tweets are available on the entered <storng style={styles.strong}>{ERROR_ERROR[`${olderDays}`]}{networkHandle}</storng>. All the upcoming tweets will appear here.</p><span style={{ padding: '0px 15px' }}><a href class="loadDemoWall btn hideBtn" onClick={this.onClickToDemoPost}>HIDE DEMO POSTS</a></span></div> :

                                                        <Fragment>
                                                            <h2>No Tweets!</h2>
                                                            <p>No Tweets are available on the entered <storng style={styles.strong}>{ERROR_ERROR[`${olderDays}`]}{networkHandle}</storng>. All the upcoming tweets will appear here.</p>
                                                            <p className="tweets-or-section">OR</p>
                                                            <p>The Tweets are older than your chosen {olderDays===2 ? `7` : `30`} days.</p>
                                                            {editor ? <div style={{ margin: '20px 0 0' }}>
                                                                <a href className="btn transBtn create-new-feed" style={styles.addBtn} onClick={this.onClickToadd}>
                                                                    Add Another Feed </a>
                                                                <a href className="loadDemoWall btn commanBtn" style={styles.showDemoPost} onClick={this.onClickToDemoPost}>SHOW DEMO POSTS</a>
                                                            </div> : null}
                                                        </Fragment>}
                                                </Fragment>

                                                : newApi.error_code === 207 || wallErrorCode === 207 ?
                                                    <Fragment>
                                                        <div className={editor ? `erro_204_fix` : ``}>
                                                            <h2>No Posts!</h2>
                                                            <p> It appears that all your posts are private. Please go to <a href={`app.taggbox.com/moderation/index/${newApi.wall.Wall.id}`} target="_blank" rel="noopener noreferrer">Moderation</a> and make some posts public.</p>
                                                        </div>
                                                    </Fragment>

                                                    : newApi.error_code === 208 || wallErrorCode === 208 ?
                                                        <Fragment>
                                                            <div className={editor ? `erro_204_fix` : ``}>
                                                                <h2>No Posts!</h2>
                                                                <p>It appears that the available posts are older than your chosen {networkHandle !== 8 ? `${networkHandle} Days` : moment(new Date(newApi.older_post * 1000)).format('LL')}
                                                                    {'. '} All the new posts will start appearing here.</p>
                                                            </div>
                                                        </Fragment>

                                                        : newApi.error_code === 209 || wallErrorCode === 209 ?
                                                            <Fragment>
                                                                <div className={editor ? `erro_204_fix` : ``}>
                                                                    <h2>No Posts!</h2>
                                                                    <p>Looks like there are no posts available with images or videos. The theme doesnt support text content.</p>
                                                                </div>
                                                            </Fragment>

                                                            : newApi.error_code === 211 || wallErrorCode === 211 ?
                                                        <Fragment>
                                                            <div className={editor ? `erro_204_fix` : ``}>
                                                                <h2>No Posts!</h2>
                                                                <p>No posts are available on the entered <storng style={styles.strong}>{ERROR_ERROR[`${olderDays}`]}{networkHandle}</storng>. All the upcoming posts will appear here.</p>
                                                            </div>
                                                        </Fragment>

                                                                : newApi.error_code === 212 || wallErrorCode === 212 ? V_EVENT ? <div className={editor ? `erro_204_fix` : ``}><h2>The Applied Theme is Not Compatible with Virtual Events.</h2></div>:<div className={editor ? `erro_204_fix` : ``}><h2>No Posts!</h2><p> Only new posts from Instagram {ERROR_ERROR[`${olderDays}`]}{networkHandle} will appear here. Existing posts from Instagram {ERROR_ERROR[`${olderDays}`]}{networkHandle} can not be retrieved.</p></div>
                                                                    : newApi.error_code === 210 || wallErrorCode === 210 ? <div className={editor ? `erro_204_fix` : ``}><h2>Social wall is not active.</h2></div>


                                                                        : null}
            </Fragment>)
    }

}
