export const updateURLForIframe = (url) => {
    if (url && url.includes('youtube.com')) {
        const urlSplits = url.split('?v=');
        if (urlSplits && urlSplits.length && urlSplits[1]) {
            url = `https://www.youtube.com/embed/${urlSplits[1]}?autoplay=1&mute=1`;
        }
    }
    else if (url && url.includes('vimeo.com')) {
        const urlSplits = url.split('/');
        if (urlSplits && urlSplits.length && urlSplits[urlSplits.length - 1]) {
            url = `https://player.vimeo.com/video/${urlSplits[3]}?autoplay=1&muted=1`;
        }
    }
    return url;
}

export const WINDOW_PATH_URL = window.location.pathname;
export const IS_EDITOR = window.editor ? true : WINDOW_PATH_URL.includes('/editor');


export const shuffleDataArray = (array) => {
    var currentIndex = array.length, randomIndex;
    while (currentIndex != 0) {
        randomIndex = Math.floor(Math.random() * currentIndex);
        currentIndex--;
        [array[currentIndex], array[randomIndex]] = [
            array[randomIndex], array[currentIndex]];
    }

    return array;
}
export const isDayTime = () => {
    try {
        const hours = new Date().getHours()
        const isDayTime = hours > 6 && hours < 20
        return isDayTime;
    }
    catch (ex) {
        return true;
    }
}