import React, { Component } from "react";
import { connect } from 'react-redux';
import { isMobile } from "react-device-detect";
import styled from 'styled-components'
import $ from "jquery";
import '../css/content-play.css'
import { getPreviewByWallID, sceneDraggable } from '../actions/sceneActions'
import SceneContent from './scene'
import ContentPlayLoader from './loader'


class Preview extends Component {
  activePartitions = []
  state = {
    data: [],
    activeScene: null,
    isFullScreen: true,
    hide: true,
  }
  componentDidMount() {
    const { wallID } = this.props;
    if (wallID) {
      getPreviewByWallID(wallID).then((response) => {
        const { scenes, response_code } = response.data;
        if (response_code == 200) {
          this.setState({ data: sceneDraggable(scenes) }, () => this.onOverlayHide())
        }
      })
    }
    this.onFullScreen();

  }
  onOverlayHide = () => {
    setTimeout(() => this.setState({ hide: false }), 1000)
  }
  onFullScreen = () => { setTimeout(() => { if (document.querySelector('#fullscreen')) document.querySelector('#fullscreen').style.display = 'none' }, 5000) }

  enableFullScreen = event => {
    let elem = document.documentElement;
    if (elem.requestFullscreen) {
      elem.requestFullscreen();
    } else if (elem.mozRequestFullScreen) { /* Firefox */
      elem.mozRequestFullScreen();
    } else if (elem.webkitRequestFullscreen) { /* Chrome, Safari and Opera */
      elem.webkitRequestFullscreen();
    } else if (elem.msRequestFullscreen) { /* IE/Edge */
      elem.msRequestFullscreen();
    }
    event.preventDefault();
  }

  render() {
    const { data, isFullScreen, hide } = this.state;
    const { screenLimitEnable, preview } = this.props

    return <>
      <section className="contentMix wde">
        {/* {(window.innerWidth < 1920 && !isMobile) ? <a id="fullscreen" onClick={this.enableFullScreen} className="commanBtn btn-sm triggerFullScreen"><i class="icon-size-fullscreen icons"></i>&nbsp; Enable Full Screen</a> : null
        } */}
        {(screenLimitEnable && !preview) ? <div className="wrappContentDivW" id="screenLimitExceed">
          <div className="contentDiv">
            <h1 className="commanH1Modal">Exceeded Screen Limit?</h1>
            <p className="commanPeraModal">Oops! Looks like you need to upgrade your plan in order to add more screens</p>
          </div>
        </div> : null}
        {hide && <ContentPlayLoader />}
        {data && data.length > 0 && <SceneContent scenes={data} />}
      </section>
    </>
  }
}


const mapStateToProps = state => {
  return {
    scenes: state.scene,
  }
}
export default connect(mapStateToProps)(Preview);